import * as React from "react";
import Button from "react-bootstrap/Button";
import CalendarButtonText from "./CalendarButtonText";
import "../../scss/whm_buttons.scss";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The props interface of {@link CalendarButton}.
 * @interface
 */
interface CalendarButtonProps {
  /**
   * The calendar button text and icon
   */
  text: string;
  /**
   * A function that handles the calendar day clicked by the user.
   * Triggers a data fetch to display the calendar details of
   * Bible readings, memory verse, and group discussion questions.
   * See {@link AdventCalendar}
   * @param num
   */
  handleNumberClick: (num: number) => void;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that returns the calendar days button.
 * The button text and icon is returned by {@link CalendarButtonText}.
 * @param props {CalendarButtonProps}
 * @returns {Button} The calendar button
 * @component
 */
const CalendarButton = (props: CalendarButtonProps): JSX.Element => {
  const date = new Date();
  return (
    <Button
      className="shadow-sm mb-3"
      //variant="ornament"
      variant={
        date.getMonth() === 11 && date.getDate() >= parseInt(props.text, 10)
          ? "ornament-past"
          : "ornament"
      }
      onClick={() => props.handleNumberClick(parseInt(props.text, 10))}
    >
      <CalendarButtonText text={props.text} />
    </Button>
  );
};

export default CalendarButton;
