import * as React from "react";
import "./CalendarItemCard.scss";
import { Card, Accordion, Row, Col, Button } from "react-bootstrap";
import { Quiz } from "../../interfaces/Interfaces";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBookReader } from "@fortawesome/free-solid-svg-icons";
import { useState } from "react";
import CalendarItemModal from "./CalendarItemModal";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The interface props for {@link CalendarItemCard}
 * @interface
 */
interface ItemCardData {
  /**
   * The key for the reading item
   */
  id: string;
  /**
   * The calendar day
   */
  d: number;
  /**
   * The daily Bible reading
   */
  v: string;
  /**
   * The memory verse
   */
  m: string;
  /**
   * The indices of blank words for the memory verse challenge
   */
  c: number[][];
  /**
   * The questions and answers for the group discussion
   */
  l: Quiz[];
  /**
   * The closing prayer
   */
  p: string;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that renders the calendar details card.
 * It shows the selected calendar day, the Bible reading, the
 * memory verse, the questions and answers for the group discussion,
 * and the memory verse challenge. The memory verse challenge
 * is rendered by the {@link CalendarItemModal}
 * @returns {Card} The calendar details container
 * @param props {ItemCardData}
 * @component
 */
const CalendarItemCard = (props: ItemCardData): JSX.Element => {
  const [modalShow, setModalShow] = useState(false);
  return (
    <Card className="shadow-sm" id={props.id}>
      <Card.Header className="CardHeaderBig">
        <Row className="text-center align-items-center align-content-center">
          <Col
            sm={6}
            md={4}
            className="text-center align-items-center align-content-center"
          >
            <h3 className="calendar-date">{props.d}</h3>
            <h5 className="calendar-date">DECEMBER</h5>
          </Col>
          <Col
            sm={6}
            md={8}
            className="text-center align-items-center align-content-center"
          >
            {props.v.split("&").map((e) => (
              <h3 className="bible-passage" key={e.toString()}>
                {e}
              </h3>
            ))}
            <span className="bible-passage">Bible</span>{" "}
            <FontAwesomeIcon icon={faBookReader} color="white" />{" "}
            <span className="bible-passage">Reading</span>
          </Col>
        </Row>
      </Card.Header>
      <Card.Header className="CardHeaderSmall">
        <Row className="text-center align-items-center align-content-center">
          <Col
            xs={4}
            sm={4}
            className="text-center align-items-center align-content-center"
          >
            <h6 className="bible-passage">DEC</h6>
            <h3 className="bible-passage">{props.d}</h3>
          </Col>
          <Col
            xs={8}
            sm={8}
            className="text-center align-items-center align-content-center"
          >
            {props.v.split("&").map((e) => (
              <h5 className="bible-passage" key={e.toString()}>
                {e}
              </h5>
            ))}
            <span className="bible-passage">Bible</span>{" "}
            <FontAwesomeIcon icon={faBookReader} color="white" />{" "}
            <span className="bible-passage">Reading</span>
          </Col>
        </Row>
      </Card.Header>
      <Card.Body>
        <Card.Text>
          <span className="memory-verse-label">Memory Verse</span>:{" "}
          {modalShow ? " " : props.m}
        </Card.Text>
        <Card.Text className="text-center">
          <span className="border-heart">
            ︵‿︵‿୨ <span className="heart">❦</span> ୧‿︵‿︵
          </span>
        </Card.Text>
        <Accordion flush={true}>
          {props.l.map((e) => (
            <Accordion.Item key={e.i.toString()} eventKey={e.i}>
              <Accordion.Header>
                <span className="accordion-question">{e.q}</span>
              </Accordion.Header>
              <Accordion.Body>{e.a}</Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
        <Card.Text className="text-center">
          <span className="border-heart">
            ︵‿︵‿୨ <span className="heart">❦</span> ୧‿︵‿︵
          </span>
        </Card.Text>
        <Card.Text>
          <span className="memory-verse-label">Prayer</span>: {props.p}
        </Card.Text>
        <Row className="justify-content-center pt-3">
          <Col xs="auto">
            <Button variant="golden" onClick={() => setModalShow(true)}>
              <span className="challenge-text">Memory Verse Challenge</span>
            </Button>
          </Col>
        </Row>
        <CalendarItemModal
          m={props.m}
          c={props.c}
          show={modalShow}
          onHide={() => setModalShow(false)}
        />
      </Card.Body>
    </Card>
  );
};

export default CalendarItemCard;
