import { Offcanvas, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import React from "react";
import "./Menu.scss";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description Props interface for {@link Menu}
 * @interface
 */
interface MenuProps {
  /**
   * Value passed to Offcanvas show property.
   * Shows the Offcanvas when set to true.
   */
  showMenu: boolean;
  /**
   * Function callback for the Offcanvas
   * onHide property. Hides the menu.
   */
  handleHideMenu: () => void;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The page navigation menu. Renders
 * an Offcanvas element. Contains
 * route paths to the Advent Calendar page, About page,
 * Outline page, and React Blog page.
 * @param props {MenuProps}
 * @returns {Offcanvas} The navigation menu.
 * @component
 */
const Menu = (props: MenuProps): JSX.Element => (
  <Offcanvas
    show={props.showMenu}
    onHide={props.handleHideMenu}
    placement="start"
  >
    <Offcanvas.Header closeButton>
      <Offcanvas.Title>Warm Hearts Ministries ❦</Offcanvas.Title>
    </Offcanvas.Header>
    <Offcanvas.Body>
      <Row className="py-2">
        <h6>Blog</h6>
      </Row>
      <Row className="px-2">
        <Link to="/React" onClick={props.handleHideMenu} className="link-menu">
          Creating A React JS App
        </Link>
      </Row>
      <Row className="py-2">
        <h6>App Project</h6>
      </Row>
      <Row className="px-2">
        <Link to="/" onClick={props.handleHideMenu} className="link-menu">
          Advent Calendar
        </Link>
      </Row>
      <Row className="px-2">
        <Link to="/about" onClick={props.handleHideMenu} className="link-menu">
          About
        </Link>
      </Row>
      <Row className="px-2">
        <Link
          to="/outline"
          onClick={props.handleHideMenu}
          className="link-menu"
        >
          Outline
        </Link>
      </Row>
      <hr />
      <Row className="py-2">
        <h6>About Us</h6>
      </Row>
      <p className="px-2">
        Read blogs about our programming pursuits and discoveries, or enjoy some
        of the apps that we create as proofs of concept while learning software
        technologies.
      </p>
      <p className="px-2">
        Through our blogs, we hope to be an encouragement those who also like to
        learn and explore software programming.
      </p>
      <p className="px-2">
        Through our software apps, we hope to pass on to others the blessings
        that we enjoy in Christ, just as God gave us people who gave us a Bible
        and led us to the love and mercy of the cross.
      </p>
      <p className="px-2">
        Jesus is the way, the truth, and the life. To God be the glory forever.
      </p>
    </Offcanvas.Body>
  </Offcanvas>
);

export default Menu;
