import React, { useEffect, useRef, useState } from "react";
import { Container, Row, Col, Collapse } from "react-bootstrap";
import { GITHUB_URL } from "../constants/Constants";
import CalendarItemCard from "../components/calendarItem/CalendarItemCard";
import CalendarItemCardPlaceHolder from "../components/calendarItem/CalendarItemCardPlaceholder";
import CalendarHeader from "../components/calendar/CalendarHeader";
import CalendarLayout from "../components/calendar/CalendarLayout";
import { AdventData } from "../interfaces/Interfaces";
import "./AdventCalendar.scss";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description Fetches the JSON document of Bible reading and memory verse
 * per calendar day selected by the user. Extracts the data which gets
 * passed on to {@link CalendarLayout} and {@link CalendarItemCard} for
 * calendar and Bible reading rendering. Typecasts the JSON data into
 * a {@link AdventData}.
 * @returns {Container} The container for the Advent Calendar page, which
 * has the Advent Calendar and Calendar Card details.
 * @component
 * @category Pages
 */
const AdventCalendar = (): JSX.Element => {
  /**
   * @description The current date
   * @constant
   */
  const date = new Date();
  /**
   * @description The selected calendar day. It is given the initial value of the current day in December.
   * @function
   */
  const [item, setItem] = useState(
    date.getMonth() === 11 && date.getDate() < 26 ? date.getDate() : 1
  );
  /**
   * @description The calendar details which comprises the Bible reading, the memory verse, and
   * group discussion questions per selected calendar day.
   * @function
   */
  const [calendarItem, setCalendarItem] = useState<AdventData>({
    d: 1,
    v: "",
    m: "",
    c: [],
    l: [],
    p: "",
  });
  /**
   * @description A toggle for opening and closing the calendar.
   * @function
   */
  const [openCalendar, setOpenCalendar] = useState(false);
  /**
   * @description Flag for scrolling the page back to the calendar header title.
   * @function
   */
  const calendarItemRef = useRef<HTMLDivElement>(null);

  /**
   * Whether the Fetch got an error response.
   */
  const [hasError, setHasError] = useState(false);

  /**
   * @description Calls the function fetchData on initial render and everytime
   * the item changes. On return, sets the calendarItem to back to its
   * original state.
   */
  useEffect(() => {
    fetchData(item);
    return () => {
      setCalendarItem({
        d: 1,
        v: "",
        m: "",
        c: [],
        l: [],
        p: "",
      });
      setHasError(false);
    };
  }, [item]);
  /**
   * @description A function that fetches the json data
   * and sets the calendarItem.
   * @param itemNum {number} The calendar day
   */
  const fetchData = (itemNum: number) => {
    const urlString = GITHUB_URL + "adventbible_" + itemNum + ".json";
    fetch(urlString)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else if (response.status === 404) {
          return Promise.reject("Error 404");
        } else {
          return Promise.reject("Error: " + response.status);
        }
      })
      .then((data) => {
        setCalendarItem(data[Object.keys(data)[0]]);
      })
      .catch((err) => {
        console.error("An error occurred: " + err);
        setHasError(true);
      });
  };
  /**
   * @description A function that opens the calendar
   */
  const handleOpenCalendar = () => {
    setOpenCalendar(!openCalendar);
  };
  /**
   * @description Handles the calendar day button clicks and updates
   * the item state.
   * @param num {number} The calendar day
   */
  const handleNumberClick = (num: number) => {
    setItem(num);
    setOpenCalendar(false);
    if (calendarItemRef && calendarItemRef.current) {
      calendarItemRef.current.scrollIntoView(true);
    }
  };
  return (
    <Container>
      <div ref={calendarItemRef} />
      <Row className="mt-3 mb-3 justify-content-center">
        <CalendarHeader />
        <Row className="align-items-center">
          <Col md={12} lg={6}>
            <Row>
              <CalendarLayout
                classname="CalendarBig"
                rows={[1, 2, 3, 4, 5, 6, 1, 3]}
                openCalendar={openCalendar}
                handleOpenCalendar={handleOpenCalendar}
                handleNumberClick={handleNumberClick}
              />
              <CalendarLayout
                classname="CalendarSmall"
                rows={[1, 2, 3, 4, 4, 4, 4, 2, 1]}
                openCalendar={openCalendar}
                handleOpenCalendar={handleOpenCalendar}
                handleNumberClick={handleNumberClick}
              />
              <CalendarLayout
                classname="CalendarXS"
                rows={[1, 2, 2, 3, 3, 3, 3, 3, 1, 1, 3]}
                openCalendar={openCalendar}
                handleOpenCalendar={handleOpenCalendar}
                handleNumberClick={handleNumberClick}
              />
            </Row>
          </Col>
          {calendarItem.v ? (
            <Collapse in={true}>
              <Col md={12} lg={6}>
                <CalendarItemCard
                  id="card-id"
                  d={item}
                  v={calendarItem.v}
                  m={calendarItem.m}
                  c={calendarItem.c}
                  l={calendarItem.l}
                  p={calendarItem.p}
                />
              </Col>
            </Collapse>
          ) : (
            <Col md={12} lg={6}>
              <CalendarItemCardPlaceHolder error={hasError} />
            </Col>
          )}
        </Row>
        <Row className="pb-3 pt-3">
          <span className="border-star text-center">
            ☆.。.:*・°☆.。.:*・°☆.。.:*・°☆.。.:*・°☆
          </span>
        </Row>
        <Row>
          <p className="text-center font-monospace mb-0 pt-1">
            Oh sing to the Lord a new song; sing to the Lord, all the earth!
            Sing to the Lord, bless his name; tell of his salvation from day to
            day.
          </p>
          <p className="text-center font-monospace mt-0">
            - Psalm 96:1-2 (ESV)
          </p>
        </Row>
        <Row className="pb-3">
          <span className="border-star text-center">
            ☆.。.:*・°☆.。.:*・°☆.。.:*・°☆.。.:*・°☆
          </span>
        </Row>
      </Row>
    </Container>
  );
};

export default AdventCalendar;
