import * as React from "react";
import "./CalendarItemCard.scss";
import { Card, Col, Row, Spinner } from "react-bootstrap";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that renders a card with spinners.
 * This shows when the data is still being fetched and hasn't finished
 * loading
 * @returns {Card} The calendar details container
 * @component
 */
const CalendarItemCardPlaceHolder = (props: {
  error: boolean;
}): JSX.Element => (
  <Card className="shadow-sm">
    <Card.Header className="CardHeaderBig" />
    <Card.Header className="CardHeaderSmall" />
    <Card.Body>
      {props.error ? (
        <>
          <p className="text-center">
            Oops. Something's not right. Please check your internet connection
            and try to refresh the browser.
          </p>
          <p className="text-center">Thank you.</p>
        </>
      ) : (
        <>
          <Row>
            <Col xs={4} />
            <Col xs={4} className="text-center">
              <Spinner animation="grow" variant="warning" size="sm" />{" "}
              <Spinner animation="grow" variant="warning" size="sm" />{" "}
              <Spinner animation="grow" variant="warning" size="sm" />{" "}
            </Col>
            <Col xs={4} />
          </Row>
          <p className="text-center">Loading Bible Reading...</p>
        </>
      )}
    </Card.Body>
  </Card>
);

export default CalendarItemCardPlaceHolder;
