import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import CalendarButton from "./CalendarButton";
import { getTreeArray } from "../../utils/getNumArray";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The props interface for the {@link CalendarPanel}.
 * @interface
 */
export interface CalendarPanelProps {
  /**
   * An array of rows that gets passed on to
   * the function getTreeArray, which returns
   * a multi-dimensional array used in rendering
   * the calendar display depending on the screen
   * size.
   */
  rows: number[];
  /**
   * A function that handles the calendar day that the user clicks
   * which triggers a data fetch. See {@link AdventCalendar}.
   * @param num {number} The calendar day that was clicked
   * by the user.
   */
  handleNumberClick: (num: number) => void;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that displays a different
 * calendar layout depending on the device screen size -
 * a christmas tree for large devices, a christmas ornament
 * for medium devices, and a nutcracker for small devices.
 * Calendar days are basically buttons returned by {@link CalendarButton}.
 * @param props {CalendarPanelProps}
 * @returns {Row} The container for the calendar
 * @component
 *
 * @author Cynthia Escano
 */
const CalendarPanel = (props: CalendarPanelProps): JSX.Element => {
  const [treeArray, setTreeArray] = useState<number[][]>([]);
  /**
   * A function that calls the getTreeArray function which returns
   * a multi-dimensional array of rows and values used in rendering
   * the calendar depending on the screen size. Gets triggered everytime
   * the user clicks a different calendar day and changes the rows props.
   */
  useEffect(() => {
    setTreeArray(getTreeArray(props.rows));
  }, [props.rows]);
  return (
    <>
      {treeArray.map((i) => (
        <Row key={i[0].toString()} className="justify-content-center">
          {i.map((j) => (
            <Col xs="auto" key={j.toString()}>
              <CalendarButton
                key={j.toString()}
                text={j.toString()}
                handleNumberClick={props.handleNumberClick}
              />
            </Col>
          ))}
        </Row>
      ))}
    </>
  );
};

export default CalendarPanel;
