import React, { useEffect, useState } from "react";
import { Col, Row, Table } from "react-bootstrap";
import { GITHUB_URL } from "../constants/Constants";
import CalendarHeader from "../components/calendar/CalendarHeader";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description Represents the outline of Bible readings data
 * @interface
 */
interface Dic {
  [key: string]: Content;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description Represents the outline of Bible readings data
 * @interface
 */
interface Content {
  /**
   * The days organized by topic
   */
  t: string;

  /**
   * The summary
   */
  s: string;

  /**
   * The Bible reading
   */
  r: Reading[];
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description Represents the outline of Bible readings data
 * @interface
 */
interface Reading {
  /**
   * The day
   */
  d: string;

  /**
   * The Bible passage
   */
  b: string;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A React component that renders an Outline page.
 * @component
 * @returns {Row} The Outline page container
 * @category Pages
 */
const Outline = (): JSX.Element => {
  const [outline, setOutline] = useState<Dic>({});
  useEffect(() => {
    fetchData();
  }, []);
  const fetchData = () => {
    const urlString = GITHUB_URL + "adventbible_outline.json";
    fetch(urlString)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else if (response.status === 404) {
          return Promise.reject("error 404");
        } else {
          return Promise.reject("some other error: " + response.status);
        }
      })
      .then((data) => {
        setOutline(data);
      })
      .catch((err) => console.error("error is: " + err));
  };
  return (
    <>
      <CalendarHeader />
      <h4 className="text-center p-3">Outline of Bible Readings</h4>
      <Row className="px-5">
        <Col sm={0} md={2} />
        <Col md={8}>
          {Object.keys(outline).map((e) => {
            return (
              <Row key={e}>
                <p>
                  <span className="fw-bold">{outline[e].t}</span> {outline[e].s}
                </p>
                <Table>
                  <thead>
                    <tr>
                      <th>Day</th>
                      <th>Bible Reading</th>
                    </tr>
                  </thead>
                  <tbody>
                    {outline[e].r.map((j) => {
                      return (
                        <tr key={j.b}>
                          <td>{j.d}</td>
                          <td>{j.b}</td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              </Row>
            );
          })}
        </Col>
        <Col sm={0} md={2} />
      </Row>
    </>
  );
};

export default Outline;
