import React from "react";
import { Accordion, Col, ListGroup, Row } from "react-bootstrap";
import CalendarHeader from "./components/calendar/CalendarHeader";

interface LicenseProps {
  licenses: string;
  repository: string;
  licenseUrl: string;
  parents: string;
}

const Licenses = () => {
  const licenses: {
    [id: string]: LicenseProps;
  } = require("./licenses.json");
  const ver = /\d+(\.\d+)*/;
  const at = /(?:@)/gi;
  return (
    <>
      <CalendarHeader />
      <h4 className="text-center p-3">Copyright Notice</h4>
      <Row>
        <Col lg={2} />
        <Col lg={8}>
          <p>Copyright 2021 Cynthia Escano</p>

          <p>
            Licensed under the Apache License, Version 2.0 (the "License"); you
            may not use this file except in compliance with the License. You may
            obtain a copy of the License at
          </p>

          <p>
            <a
              href="http://www.apache.org/licenses/LICENSE-2.0"
              target="_blank"
              rel="noreferrer"
            >
              http://www.apache.org/licenses/LICENSE-2.0
            </a>
          </p>

          <p>
            Unless required by applicable law or agreed to in writing, software
            distributed under the License is distributed on an "AS IS" BASIS,
            WITHOUT WARRANTIES OR CONDITIONS OF ANY KIND, either express or
            implied. See the License for the specific language governing
            permissions and limitations under the License.
          </p>
          <h6>Scripture Quotations</h6>
          <p>
            “Scripture quotations are from The ESV® Bible (The Holy Bible,
            English Standard Version®), copyright © 2001 by Crossway, a
            publishing ministry of Good News Publishers. Used by permission. All
            rights reserved.”
          </p>
        </Col>
        <Col lg={2} />
      </Row>
      <Row>
        <Col lg={2} />
        <Col lg={8}>
          <h6>Thank you to open source projects!</h6>
          <Accordion className="pt-2">
            {Object.keys(licenses).map((e: string) => {
              const version = e.match(ver);
              const library = e
                .replace(at, "")
                .replace(version ? version[0] : "", "");
              return (
                <Accordion.Item key={e.toString()} eventKey={e}>
                  <Accordion.Header>{library}</Accordion.Header>
                  <Accordion.Body>
                    <ListGroup>
                      <ListGroup.Item>Version: {version}</ListGroup.Item>
                      <ListGroup.Item>
                        License: {licenses[e].licenses}
                      </ListGroup.Item>
                      <ListGroup.Item
                        action
                        href={licenses[e].repository}
                        target="_blank"
                        rel="noreferrer"
                      >
                        Repository
                      </ListGroup.Item>
                      <ListGroup.Item
                        action
                        href={licenses[e].licenseUrl}
                        target="_blank"
                      >
                        License URL
                      </ListGroup.Item>
                    </ListGroup>
                  </Accordion.Body>
                </Accordion.Item>
              );
            })}
          </Accordion>
        </Col>
        <Col lg={2} />
      </Row>
    </>
  );
};

export default Licenses;
