import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGripLines } from "@fortawesome/free-solid-svg-icons";
import "./CalendarHeaderButton.scss";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The props interface for the {@link CalendarHeaderButton}.
 * @interface
 */
interface CalendarHeaderButtonProps {
  /**
   * Flag for displaying the calendar
   */
  openCalendar: boolean;
  /**
   * A function that handles displaying or collapsing
   * the calendar.
   * @param open {boolean} Flag for displaying the calendar
   */
  handleOpenCalendar: (open: boolean) => void;
}

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that returns a button which toggles the
 * calendar display on small devices.
 * @param props {CalendarHeaderButtonProps}
 * @returns {(Row|Button)} The calendar header button
 * @component
 */
const CalendarHeaderButton = (props: CalendarHeaderButtonProps) => (
  <Row className="justify-content-end calendar-header-button">
    <Col xs="auto">
      <Button
        onClick={() => props.handleOpenCalendar(props.openCalendar)}
        aria-controls="collapse-calendar"
        aria-expanded={props.openCalendar}
        variant="transparent"
      >
        <FontAwesomeIcon icon={faGripLines} />
      </Button>
    </Col>
  </Row>
);

export default CalendarHeaderButton;
