import React, { useEffect, useState } from "react";
import { Button, Modal, Row } from "react-bootstrap";
import { formatVerseArray } from "../../utils/getBlankVerse";
import "./CalendarItemCard.scss";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description The props interface for {@link CalendarItemModal}
 * @interface
 */
interface CalendarItemModalProps {
  /**
   * The memory verse
   */
  m: string;
  /**
   * The indices of blank words for the memory verse challenge
   */
  c: number[][];
  /**
   * Toggle for showing the modal container
   */
  show: boolean;
  /**
   * Hides the modal container
   */
  onHide: () => void;
}

/**
 * @description A component that renders a modal that shows
 * the memory verse challenge questions.
 * @param props {CalendarItemModalProps}
 * @returns {Modal} The container for the memory verse challenge questions
 * @component
 */
const CalendarItemModal = (props: CalendarItemModalProps) => {
  const [item, setItem] = useState(0);
  const [verseArray, setVerseArray] = useState<string[]>();
  useEffect(() => {
    setVerseArray(formatVerseArray(props.m, props.c));
    return () => {
      setItem(0);
      setVerseArray([]);
    };
  }, [props.m, props.c]);
  const handleNext = () => {
    setItem(item + 1);
  };
  const startOver = () => {
    setItem(0);
  };
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Memory Verse Challenge
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {verseArray ? (
          verseArray[item] ? (
            <Row className="align-items-center my-2 mx-5">
              <p className="text-center">{verseArray[item]}</p>
            </Row>
          ) : (
            <Row className="align-items-center my-2">
              <p className="text-center">End of the challenge.</p>
            </Row>
          )
        ) : undefined}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="golden" onClick={handleNext}>
          <span className="challenge-text">Next</span>
        </Button>
        <Button variant="golden" onClick={startOver}>
          <span className="challenge-text">Start Over</span>
        </Button>
        <Button variant="golden" onClick={props.onHide}>
          <span className="challenge-text">Close</span>
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default CalendarItemModal;
