import React, { useState } from "react";
import { Container } from "react-bootstrap";
import { Link } from "react-router-dom";
import Header from "./components/header/Header";
import Menu from "./components/menu/Menu";
import NavRouter from "./components/navigation/NavRouter";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A React component that renders the page {@link Header}
 * and the navigation {@link Menu} page. Page navigations are handled
 * by the {@link NavRouter}.
 * @component
 * @returns {(Header|Menu)} {@link Header} & {@link Menu} Container
 */
const App = (): JSX.Element => {
  const d = new Date();
  const currentYear = d.getFullYear();
  /**
   * Toggle for showing the Offcanvas menu
   */
  const [showMenu, setShowMenu] = useState(false);
  /**
   * Shows the menu items
   */
  const handleShowMenu = () => {
    setShowMenu(true);
  };
  /**
   * Hides the menu items
   */
  const handleHideMenu = () => {
    setShowMenu(false);
  };
  return (
    <Container fluid>
      <Header handleShowMenu={handleShowMenu} />
      <Menu showMenu={showMenu} handleHideMenu={handleHideMenu} />
      <NavRouter />
      <footer>
        {" "}
        <small>
          &copy; Copyright {currentYear} Cynthia Escano |{" "}
          <Link to="/licenses">Copyright Notice</Link>
        </small>{" "}
      </footer>
    </Container>
  );
};

export default App;
