function formatVerse(verse:string, indices:number[]): string {
    let strSplit = verse.split(' ');
    indices.map(e => {
        return strSplit[e] = '__________';
    });
    return strSplit.join(' ');
}

export function formatVerseArray(verse:string, indices:number[][]): string[] {
    let strArray:string[] = [];
    indices.map(e => {
        return strArray.push(formatVerse(verse, e));
    })
    return strArray;
}
