export function getTreeArray(rows: number[]): number[][] {
  const tree: number[][] = [];
  function getCols(): number[] {
    const j: number[] = [];
    for (let i = 25; i > 0; i--) {
      j.push(i);
    }
    return j;
  }
  let k = 0;
  rows.map((e) => {
    const j = [];
    for (let i = 0; i <= e - 1; i++) {
      j.push(getCols()[k]);
      k += 1;
    }
    return tree.push(j);
  });
  return tree;
}
