import React from "react";
import { Route, Switch } from "react-router-dom";
import AdventCalendar from "../../pages/AdventCalendar";
import About from "../../pages/About";
import Outline from "../../pages/Outline";
import ReactBlog from "../../pages/ReactBlog";
import Licenses from "../../Licenses";

/**
 * @license Apache-2.0. See license page.
 * @copyright Cynthia Escano 2021
 * @description A component that defines the navigation route paths
 * using the react-router-dom library.
 * @returns {(Switch|Route)} The route paths
 * @component
 */
const NavRouter = () => (
  <Switch>
    <Route exact path="/">
      <AdventCalendar />
    </Route>
    <Route path="/about">
      <About />
    </Route>
    <Route path="/outline">
      <Outline />
    </Route>
    <Route path="/react">
      <ReactBlog />
    </Route>
    <Route path="/licenses">
      <Licenses />
    </Route>
  </Switch>
);

export default NavRouter;
